  right: 0;
  bottom: 0;
  margin: auto;
  position: absolute;
  font-size: 14px;
}

.header-expanse__contacts {
  padding-right: 1.875rem;
  display: flex;
  align-items: center;
  column-gap: 1.875rem;
}
@media (max-width: 81.25em) {
  .header-expanse__contacts {
    column-gap: clamp( 0.3125rem , 0rem  +  1.5625vw , 1.875rem );
  }
}

.header-expanse__email {
  font-weight: 500;
  text-decoration: none;
  color: #000;
  white-space: nowrap;
}
@media only screen and (max-width: 113.75em) {
  .header-expanse__email {
    max-width: 7.5rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.header-expanse__wats {
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background-color: rgb(72, 201, 95);
}

.icon-watsapp {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.3125rem;
  color: #fff;
}

.header-expanse__actions {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  column-gap: 1rem;
}

/*--------------------END expanse-header----------------------*/
/*--------------------expanse-sidebar-left----------------------*/
.expanse__nav {
  padding: 0.75rem 2rem;
}

.expanse-sidebar__logo {
  max-width: 5.0625rem;
  margin-bottom: 2.5rem;
}
.expanse-sidebar__logo img {
  max-width: 100%;
}

.expanse-menu__item {
  list-style-type: none;
}
.expanse-menu__item:not(:last-child) {
  margin-bottom: 1rem;
}

.expanse-menu__link {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
  padding: 0.3125rem 1.5rem;
  background-color: #F1F1F5;
  text-decoration: none;
  color: #000;
  border-radius: 0.625rem;
}
.expanse-menu__link.active {
  margin-left: 0.375rem;
  background-color: #3A8BF3;
  color: #fff;
}
.expanse-menu__link.active:before {
  display: block;
}
.expanse-menu__link:before {
  content: "";
  display: none;
  position: absolute;
  left: -0.375rem;
  top: 50%;
  margin-top: -1rem;
  height: 2rem;
  width: 0.125rem;
  vertical-align: middle;
  background-color: #3A8BF3;
  border-radius: 2px 5px 5px 2px;
}

/*--------------------END expanse-sidebar-left----------------------*/
/*--------------------expanse-sidebar----------------------*/
/*--------------------END expanse-sidebar----------------------*/
/*--------------------expanse-template----------------------*/
.expanse {
  height: 100vh;
  padding: 1.25rem 0;
  display: flex;
}

.expanse-nav {
  flex: 0 0 18.75rem;
}

.expanse__content {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}

.expanse__container {
  display: flex;
  flex: 1 1 auto;
  height: calc(100% - 5rem);
}

.expanse__main {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}

.expanse__field {
  flex: 1 1 auto;
  padding-left: 1.875rem;
  overflow-y: auto;
  background-color: #fff;
  box-shadow: 0px 10px 15px 0px rgba(71, 93, 169, 0.04), 0px -6px 15px 0px rgba(71, 93, 169, 0.03);
}

.expanse__chat {
  flex: 0 0 auto;
}

.expanse__sidebar {
  flex: 0 0 23.875rem;
  padding: 1.875rem 2.5rem 0 2.5rem;
  background-color: #F8F9FC;
}

/*--------------------END expanse-template----------------------*/
/*--------------------offers-filter----------------------*/
.offers-filter__group:not(:last-child) {
  margin-bottom: 1.875rem;
}

.offers-filter__items {
  display: flex;
  flex-wrap: wrap;
  gap: 0.625rem;
}

.offers-filter__title {
  margin-bottom: 0.875rem;
  font-weight: 500;
}

.offers-filter__input {
  position: absolute;
  box-sizing: border-box;
  width: 0.0625rem;
  height: 0.0625rem;
  display: block;
  left: 50%;
  top: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
}
.offers-filter__input:checked + .offers-filter__name {
  background-color: #3A8BF3;
  color: #fff;
}

.offers-filter__name {
  height: 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #fff;
  color: #000;
  border-radius: 0.3125rem;
  font-size: 0.875rem;
}

/*--------------------END offers-filter----------------------*/