/*--------------------ui----------------------*/
.page{
	padding: rem(30) 0;

	&-main{

		@include min-screen($md){
			
			height: 100vh;
		}

	}
	&-enter{

		.page-main__forms{
			.sidebar-form{
				@include max-screen($sm){
					display: none;
				}

			}

			&.active{
					.page-main__form-display{
						display: none;
					}
				.sidebar-form{
					@include max-screen($sm){
						display: block;
					}

				}
			}
		}

	}
	&-buyer,
	&-agency{
		.page-main__visual{
			@include max-screen($xs){
				aspect-ratio: auto;
				padding: 0;
			}

			.logo{
				@include max-screen($xs){
					margin-bottom: rem(20);
				}
			}
		}
		.page-main__hero,
		.page-main__title{
			@include max-screen($xs){
				display: none;
				
			}
		}

		.page-main__coverage,
		.main-page__variants{
			@include max-screen($xs){
				display: none;
			}
		}
		.page-main__wrapper{
			@include max-screen($xs){
				gap: 0;
			}
		}
		.page-main__content{
			@include max-screen($xs){
				gap: 0;
			}
		}
	}
}
.btn{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: rem(48);
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: $accent;
  padding: rem(5) rem(24);
  font-size: rem(16);
  font-family: "Raleway", sans-serif;
  // text-transform: uppercase;
  border-radius: rem(10);
  border: 1px solid transparent;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .1s ease;
  
  &-fw{
   width: 100%;
  }
  @media (any-hover: hover) { //    
  		cursor: pointer;	
	  &:hover,&:active{
	   border-color: $accent;
	   background-color: transparent;
	   color: $accent;
	  }
  	}

  &-dark{
  	background-color: $gray-dark;
  	color: #fff;
  }
  &-empty{
  	background-color: transparent;
  	color: $accent;
  	border-color: $accent;

  	@media (any-hover: hover) { //    
	  &:hover,&:active{
	   border-color: transparent;
	   background-color: $accent;
	   color: #fff;
	  }
  	}
  }
  &-mini{
  	height: rem(36);
  	border-radius: rem(100);
  	background-color: $gray;
  	color: $text;
  	border-color: transparent;

  	@media (any-hover: hover) { //    
	  &:hover,&:active{
	   border-color: transparent;
	   background-color: $gray-dark;
	   color: #fff;
	  }
  	}
  }

   
}
.link{
	color: $accent;
	text-decoration: none;
	transition:olor .2s ease;
	@media (any-hover: hover) { //    
			&:hover{
				color: $text;
			}
		}
}
h1{
	@include adaptive-value-rem('font-size', 40, 24, $maxWidth, $minWidth, 1);
	line-height: math.div(48,40);
	text-transform: uppercase;
}
h2{
	font-size: rem(24);
	line-height: math.div(28,24);
	font-weight: 500;
}

.simplebar-scrollbar{
	width: 2px;
	left: auto;
	background-color: $accent;
}

